.task-form {
  background: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
  max-width: 500px;
  margin: 0 auto;
  transition: all 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.task-form:hover {
  box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.12);
}

.task-form h2 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  margin-top: -10px;
  color: #333;
  text-align: center;
}

.task-form input,
.task-form textarea,
.task-form .form-row select {
  width: 90%;
  padding: 12px 14px;
  margin-bottom: 14px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border 0.2s ease-in-out;
}

.task-form input:focus,
.task-form textarea:focus,
.task-form select:focus {
  border-color: #4a90e2;
  box-shadow: 0px 0px 4px rgba(74, 144, 226, 0.4);
}

.task-form textarea {
  min-height: 80px;
  resize: vertical;
}

.task-form .form-row {
  display: flex;
  gap: 12px;
  width:100%;
  /* background-color: #333; */
  align-items: center;
  justify-content: center;
}
.task-form .form-row select{
    width:97%;
}

.task-form button {
  width: 96%;
  background: #4a90e2;
  color: #fff;
  font-weight: 600;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.task-form button:hover {
  background: #357abd;
}
