/* Auth.css */
.auth-container {
  width: 100%;
  max-width: 380px;
  margin: 80px auto;
  padding: 30px;
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.12);
  text-align: center;
  font-family: "Segoe UI", sans-serif;
}

.auth-container h2 {
  margin-bottom: 20px;
  color: #333;
  font-size: 1.8rem;
  font-weight: bold;
}

.auth-container form {
  display: flex;
  flex-direction: column;
}

.auth-container input {
  margin: 10px 0;
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  outline: none;
  transition: all 0.3s ease;
}

.auth-container input:focus {
  border-color: #4a90e2;
  box-shadow: 0px 0px 6px rgba(74, 144, 226, 0.3);
}

.auth-container button {
  margin-top: 15px;
  padding: 12px;
  background: #4a90e2;
  border: none;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.auth-container button:hover {
  background: #357ab8;
}

.auth-links {
  margin-top: 20px;
  font-size: 0.95rem;
  color: #555;
}

.auth-links a {
  color: #4a90e2;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.auth-links a:hover {
  color: #2c5d94;
  text-decoration: underline;
}
