.task-card {
  background: #ffffff;
  border-radius: 10px;
  padding: 16px 18px;
  margin-bottom: 16px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items:center;
}
.select{
  display: flex;
  gap: 10px;
}

.task-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.12);
}

/* Left side content */
.task-details {
  max-width: 80%;
}

.task-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #2d3748;
  margin-bottom: 6px;
}

.task-desc {
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 8px;
}

.due-date {
  font-size: 0.9rem;
  color: #333;
  /* font-weight: bold; */
  color: green;
}

.due-date.overdue {
  color: red;
  font-weight: 300;
}

/* Dropdowns */
.task-card label {
  font-size: 0.85rem;
  font-weight: 500;
  color: #333;
  margin-right: 10px;
}

.task-card select {
  margin-left: 6px;
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background: #f9f9f9;
  font-size: 0.9rem;
  cursor: pointer;
  transition: border-color 0.2s ease;
}

.task-card select:focus {
  outline: none;
  border-color: #3182ce;
  background: #fff;
}

/* Right side delete button */
.delete-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  color: #e53e3e;
  transition: transform 0.2s ease, color 0.2s ease;
}

.delete-btn:hover {
  transform: scale(1.2);
  color: #c53030;
}

/* Priority dropdown colors */
.task-card select option[value="low"] {
  color: green;
}

.task-card select option[value="medium"] {
  color: orange;
}

.task-card select option[value="high"] {
  color: red;
}

/* Status dropdown colors */
.task-card select option[value="pending"] {
  color: #d69e2e; /* yellow */
}

.task-card select option[value="in-progress"] {
  color: #3182ce; /* blue */
}

.task-card select option[value="completed"] {
  color: #38a169; /* green */
}
